import React, {Component} from 'react';
import image from "../images/grey.jpg";

class Working extends Component {
    render(){
        return(
            <React.Fragment>
                <h3 className="mt-30 mx-auto text-center width-33" id="HowItsWork">Comment ça marche ?</h3>
                <div className="mx-auto mt-3 border-green"/>
                <img src={image} className="mt-5 img-fluid"/>
            </React.Fragment>
        );
    }
}
export default Working;