import React, {Component} from 'react';
import logo from "../images/nasa-logo.png";

class Header extends Component {
    render(){
        return(
            <nav class="navbar navbar-expand-lg navbar-light bg-white">
                <a class="navbar-brand" href="#">
                    <img src={logo} className="logo"></img>
                </a>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarNav">
                    <ul class="navbar-nav margin-auto">
                        <li class="nav-item active">
                            <a class="nav-link" href="#about">A propos<span class="sr-only">(current)</span></a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="#howItsWorks">Comment ça marche</a>
                        </li>
                    </ul>
                    <button class="btn btn-success" type="button">Contactez-nous</button>
                </div>
            </nav>
        );
    }
}
export default Header;
