import React, {Component} from 'react';

class Contact extends Component{
    render(){
        const isSendSuccess = window.location.search.indexOf('email=sended') !== -1; // Si il y a le paramètre
        return(
            <div className="row mt-650">
                <div className="mt-auto mb-auto col-md-3 offset-md-1">
                    <div className="text-center border-yellow-rounded padding-20">
                        <p>Contact<br/> 
                        06 89 51 71 93<br/>
                        contact@smartborne.fr
                        </p>
                    </div>
                </div>
                <div className="col-md-6 offset-md-1">
                    <form {...this.props} action="https://formspree.io/contact@smartborne.fr" method="POST">
                        {isSendSuccess === true &&
                            <div className="mb-5 alert alert-success" role="alert">
                                Your message has been successfully send.
                                <button type="button" className="close" data-dismiss="alert" aria-label="Close" onClick={(e) => {window.location.search=""; this.setState({ isSendSuccess: false }) }}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                        }
                        <div class="form-group">
                            <label for="exampleFormControlInput1">Email address</label>
                            <input type="email" class="form-control" id="exampleFormControlInput1" placeholder="name@example.com"/>
                        </div>
                        <div class="form-group">
                            <label for="exampleFormControlInput2">Votre nom</label>
                            <input type="text" class="form-control" id="exampleFormControlInput2" placeholder="name"/>
                        </div>
                        <div class="form-group">
                            <label for="exampleFormControlTextarea1">Votre message</label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                        </div>
                        <input type="submit" class="btn btn-outline-dark" value="Envoyer"/>                
                        <input type="hidden" name="_next" value="https://pyozer.github.io/#contact?email=sended" />
                    </form>
                </div>
            </div>
        );
    }
}
export default Contact; 