import React, {Component} from "react";
import facebook from '../images/facebook.png';
import twitter from '../images/twitter.png';
import youtube from '../images/youtube.png';



class Footer extends Component {
    render(){
        return(
            <footer class="mt-5 site-footer">
                <a href="#footer" className="mr-3"> 
                    <img src={facebook} className="logo-social"/>
                </a>
                <a href="#footer" className="mr-3">
                    <img src={youtube} className="logo-social"/>
                </a>
                <a href="#footer">
                    <img src={twitter} className="logo-social"/>
                </a>
                <p>
                    <a href="#cgu">Mentions légales</a>
                </p>
                <p>Tous droits réservés Smart born 2018</p>
                <p>
                    <a href="#">Back to top</a>
                </p>
            </footer>
        )
    }
}
export default Footer;